import { AnyAction } from "redux";
import { ThunkAction } from "redux-thunk";

import { googleAPI } from "config";
import { fetchDetails } from "../actionCreators";
import { Redirect, RootStateType } from "types";

export interface ThunkFetchIndividualBookArgs {
    redirect: Redirect;
    id: string;
}
const thunkFetchIndividualBook = ({ redirect, id }: ThunkFetchIndividualBookArgs): ThunkAction<void, RootStateType, unknown, AnyAction> => {
    return async dispatch => {
        const path = googleAPI + id;
        dispatch(fetchDetails(path));
        redirect.individualBook();
    };
};

export default thunkFetchIndividualBook;
